@import './_variables.scss';
$navHeight: 73px;

body {
 font-family: 'Lato', sans-serif;
 position: relative;
}

.chapters-title {
 text-align: center;
 font-family: 'Montserrat', sans-serif;
 letter-spacing: 5px;
 position: relative;
 z-index: -1;

 p {
 font-size: 24px;
 margin: 0;
 text-transform: uppercase;
 }
}

div.lead-quote {
 font-size: 20px;
 background-color: rgba(252,248,227, 0.5);
 padding: 20px 5px;
 margin-bottom: 20px;
 border-top-left-radius: 10px;
 border-bottom-right-radius: 10px;
}

.lead-quote {
 p {
 margin-bottom: 0;
 }
}

.lead-quote::before {
 font-family: 'PT Serif', serif;
 color: $sc-primary-color;
 content: 'â€œ';
 font-size: 4em;
 line-height: 0.1em;
 margin-right: 0.05em;
 vertical-align: -0.4em;
}

.first-reading-paragraph {
 padding: 15px 0 0 0;
}

.first-reading-paragraph::first-letter, .chapter-text::first-letter {
 font-size: 45px;
 line-height: 1.5px;
 padding-right: 5px;
 padding-left: 5px;
}

.navbar-brand {
 img {
 margin-right: 5px;
 }
}

#main-nav.navbar-dark {
 .nav-item {
 .nav-link.menu-link, .nav-link.menu-link:hover, .nav-link.menu-link:focus, .nav-link {
 color: white;
 }
 }
}

#main-nav.navbar-light {
 .nav-item {
 .nav-link.menu-link, .nav-link.menu-link:hover, .nav-link.menu-link:focus, .nav-link {
 color: black;
 }
 }
}

.container.first, .section.first {
 padding-top: 100px;
}

// Buttons
.btn-outline-success {
 color: #6eb252;
 border-color: #6eb252;
}

.btn-outline-success:hover {
 background-color: #6eb252;
 border-color: #6eb252;
}

.btn-outline-solarchapter {
 color: $sc-primary-color;
 border-color: rgb(212, 197, 17);
}

.btn-outline-solarchapter:hover {
 background-color: rgb(212, 197, 17);
 border-color: rgb(212, 197, 17);
}

.btn-solarchapter {
 background-color: rgb(212, 197, 17);
 border-color: rgb(212, 197, 17);
}

.btn-solarchapter:hover {
 background-color: rgb(212, 197, 17);
 border-color: rgb(212, 197, 17);
}

.btn-solarchapter-sec {
 background-color: $sc-secondary-color;
 border-color: $sc-secondary-color;
}

.btn-solarchapter-sec:hover {
 background-color: $sc-secondary-color;
 border-color: $sc-secondary-color;
}

// Header Navigation
#main-nav-container {
 position: relative;
 z-index: 1000; // Should take precedent over anything
}

.navbar-dark .sc-navbar.navbar-toggler {
 color: rgba(255,255,255,1);
 border-color: rgba(255,255,255,1);
}

.dropdown-item.active, .dropdown-item:active {
 color: #fff;
 text-decoration: none;
 background-color: $sc-primary-color;
}

#main-nav {
 height: $navHeight;
 position: absolute;
 width: 100%;
 top: 0;
 left: 0;
 padding: 35px;
 z-index: 1;


 .nav-item {
 margin-left: 5px;
 margin-right: 5px;

 .nav-link.menu-link, .nav-link.menu-link:hover, .nav-link.menu-link:focus, .nav-link {
 color: white;
 }
 }
}

@media(max-width: 576px) {
 #main-nav {
 .navbar-collapse {
 border-radius: 5px;
 background-color: white;
 margin-top: 10px;
 padding: 10px 20px;
 
 .navbar-nav, form {
 padding: 10px;
 }
 
 .nav-item {
 .nav-link {
 color: black;
 }
 }
 }
 }

 .container.full {
 padding-right: 0;
 padding-left: 0;

 .row {
 margin-right: 0;
 margin-left: 0;

 .col {
 padding-right: 0;
 padding-left: 0;
 }
 }
 }

 .container.video {
 padding: 10px;
 }

 .section-content {
 video {
 height: 500px;
 object-fit: cover;
 }
 }
}

.nav-form {
 padding-right: .5rem;
 padding-left: .5rem;

 button {
 letter-spacing: 0.5px;
 }
}

.container {
 position: relative;
}

.container .video {
 position: absolute;
 top: 0;
 height: 100%;
}

.hero-content {
 height: 100%;
}

.section-content {
 position: relative;
 // padding: 10px 5px;
 background-repeat: no-repeat;
 background-size: cover;
 background-position: center center;

 h1, p {
 color: white;
 }

 .lead {
 text-align: center;
 }

 video {
 width: 100%;
 vertical-align: bottom;
 }
}

 .section {
 position: relative;
 overflow: hidden;
 }

mark.yellow {
 background: rgba(212, 197, 17, 0.8);
}

mark.green {
 background: #6eb252;
}

.section-content::before {
 content: '';
 position: absolute;
 top: 0;
 left: 0;
 background-color: rgba(0, 0, 0, 0.45);
 width: 100%;
 height: 100%;
}

.container.full-height, .row.full-height {
 position: absolute;
 height: 100%;
 top: 0;
 left: 0;
 bottom: 0;
 right: 0;
}

span.block {
 display: block;
}

.daun {
 display: inline-block;
 content: url('/assets/media/daun.png');
}

.card-title {
 font-family: 'Montserrat', sans-serif;
 text-transform: uppercase;
 text-align: center;
 letter-spacing: 2px;
}

.card-title.left {
 text-align: left;
}

.card-title.right {
 text-align: right;
}


.card-title.left {
 text-align: left;
}

.card-title.right {
 display: inline-flex;
 transform: translateX(-50%);

 .slide {
 display: inline-block;
 align-items: center;
 }
}


.card-title.light {
 color: white;
}

.bg-auto-carousel {
 display: inline-flex;
 transform: translate(0, -50%);
}

.bg-auto-carousel.animate {
 animation: bg-auto-carousel linear infinite;
 -webkit-animation: bg-auto-carousel linear infinite;
 /* Safari: not able to get full width on load */
 animation-delay: 0.1s;
 -webkit-animation-delay: 0.1s;
}

.auto-carousel {
 display: inline-flex;
}

.auto-carousel.animate {
 animation: auto-carousel linear infinite;
 -webkit-animation: auto-carousel linear infinite;
 /* Safari: not able to get full width on load */
 animation-delay: 0.1s;
 -webkit-animation-delay: 0.1s;
}

@keyframes bg-auto-carousel {
 from { transform: translate(0, -50%) }
 to { transform: translate(-50%, -50%) }
}

@keyframes auto-carousel {
 from { transform: translateX(0) }
 to { transform: translateX(-50%) }
}

.fade-in {
 animation: fadeIn 1s linear 1;
}

@keyframes fadeIn {
 from { opacity: 0; }
 to { opacity: 1; }
}

// Footer
footer {
 padding: 50px 0px 20px 0;

 .icon-list {
 padding-top: 15px;

 i:first-of-type {
 margin-left: 0px;
 }

 i {
 margin: 0 15px;
 }
 }

 a, a:hover {
 color: black;
 }
}

.bg-image {
 height: 100%;
 width: 100%;
 background-position: center;
 background-repeat: no-repeat;
 background-size: cover;
}

